@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.about-us {
  margin-bottom: 58px;
}
.about-us::before {
  content: "";
  height: 6px;
  width: 100%;
  position: absolute;
  background-image: url("../img/zigzag-line-mobile.svg");
  background-size: contain;
  background-repeat: repeat-x;
}
@media (min-width: 768px) {
  .about-us {
    margin-bottom: 83px;
  }
  .about-us::before {
    background-image: url("../img/zigzag-line-tablet.svg");
    height: 17px;
    background-position: 9px 0;
    margin-top: 15px;
  }
}
@media (min-width: 1150px) {
  .about-us {
    margin-bottom: 91px;
  }
  .about-us::before {
    background-image: url("../img/zigzag-line-desktop.svg");
    height: 23px;
    background-position: 23px 0;
    margin-top: 36px;
  }
}

.about-us__wrapper {
  width: 320px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .about-us__wrapper {
    width: 768px;
    margin: 0 auto;
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .about-us__wrapper {
    width: 1150px;
    margin: 0 auto;
    padding: 0 225px;
  }
}

.about-us__title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  margin: 0;
  padding: 36px 0 28px;
  color: #000000;
}
@media (min-width: 768px) {
  .about-us__title {
    font-size: 43px;
    line-height: 55px;
    padding: 86px 0 39px;
  }
}
@media (min-width: 1150px) {
  .about-us__title {
    font-size: 53px;
    line-height: 57px;
    padding: 127px 0 39px;
  }
}

.about-us__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  row-gap: 32px;
}
@media (min-width: 768px) {
  .about-us__list {
    grid-template-columns: 325px 325px;
    column-gap: 10px;
    row-gap: 60px;
  }
}
@media (min-width: 1150px) {
  .about-us__list {
    column-gap: 25px;
  }
}

.about-us__item {
  position: relative;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  padding-top: 54px;
  color: #000000;
}
.about-us__item--like, .about-us__item--manual, .about-us__item--support {
  padding-top: 50px;
}
.about-us__item--materials svg {
  position: absolute;
  top: 0;
  left: 7px;
  width: 33px;
  height: 38px;
}
.about-us__item--style svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 47px;
  height: 27px;
}
.about-us__item--like svg {
  position: absolute;
  top: 0;
  left: 6px;
  width: 34px;
  height: 27px;
}
.about-us__item--manual svg {
  position: absolute;
  top: 0;
  left: 2px;
  width: 35px;
  height: 34px;
}
.about-us__item--support svg {
  position: absolute;
  top: 0;
  left: -6px;
  width: 55px;
  height: 23px;
}
.about-us__item--box svg {
  position: absolute;
  top: 0;
  left: 2px;
  width: 33px;
  height: 32px;
}
@media (min-width: 768px) {
  .about-us__item {
    font-size: 20px;
    line-height: 22px;
    padding-top: 50px;
  }
  .about-us__item--manual, .about-us__item--materials {
    padding-top: 57px;
  }
}

@media (min-width: 768px) {
  .about-us__item:nth-child(1) {
    order: -4;
  }

  .about-us__item:nth-child(4) {
    order: -3;
  }

  .about-us__item:nth-child(2) {
    order: -2;
  }

  .about-us__item:nth-child(5) {
    order: -1;
  }
}
.button {
  display: block;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  color: #444444;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  padding: 14px 0;
  border: 2px solid #444444;
  background-color: #ffffff;
  cursor: pointer;
}
.button--modal {
  line-height: 16px;
  background-color: #63d1bb;
  color: #ffffff;
  border: 0;
  width: 100%;
  padding: 18px 0;
}
.button:hover {
  color: #ffffff;
  background-color: #444444;
  transition: 250ms;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.contacts {
  position: relative;
}
.contacts::before {
  content: "";
  height: 6px;
  width: 100%;
  position: absolute;
  background-image: url("../img/zigzag-line-mobile.svg");
  background-size: contain;
  background-repeat: repeat-x;
}
@media (min-width: 768px) {
  .contacts {
    margin-top: 111px;
  }
  .contacts::before {
    background-image: url("../img/zigzag-line-tablet.svg");
    background-position: -24px 0;
    height: 17px;
    margin-top: -42px;
  }
}
@media (min-width: 1150px) {
  .contacts {
    margin-top: 62px;
  }
  .contacts::before {
    background-image: url("../img/zigzag-line-desktop.svg");
    height: 23px;
    margin-top: 3px;
    background-position: 20px 0;
  }
}

.contacts__wrapper {
  position: relative;
  padding: 0 30px;
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contacts__wrapper {
    padding: 0 50px;
    margin: 0 auto;
    width: 768px;
  }
}
@media (min-width: 1150px) {
  .contacts__wrapper {
    display: grid;
    grid-template-columns: 40% 60%;
    grid-template-areas: "contacts__title contacts__map" "contacts__list contacts__map" "contacts__write contacts__map";
    width: 1150px;
    padding: 0 50px;
    padding-left: 225px;
    margin-bottom: 105px;
  }
}

.contacts__title {
  grid-area: contacts__title;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin: 0;
  padding-top: 43px;
}
@media (min-width: 768px) {
  .contacts__title {
    font-size: 43px;
    line-height: 50px;
  }
  .contacts__title::before {
    content: url("../img/icon-flag.svg");
    position: absolute;
    top: 20px;
    right: 7%;
    width: 47px;
    height: 81px;
  }
}
@media (min-width: 1150px) {
  .contacts__title {
    font-size: 53px;
    line-height: 57px;
    padding-top: 176px;
  }
  .contacts__title::before {
    right: 86%;
    top: 29%;
  }
}

.contacts__list {
  grid-area: contacts__list;
  margin: 0;
}
@media (min-width: 768px) {
  .contacts__list {
    display: flex;
    gap: 51px;
    padding-top: 49px;
  }
}
@media (min-width: 1150px) {
  .contacts__list {
    display: block;
    padding-top: 35px;
  }
}

.contacts__item {
  display: flex;
  gap: 33px;
  margin-top: 29px;
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
}
@media (min-width: 768px) {
  .contacts__item {
    font-size: 17px;
    margin: 0;
    gap: 24px;
  }
}
@media (min-width: 1150px) {
  .contacts__item {
    margin-bottom: 30px;
  }
}

.contacts__value {
  margin: 0;
}

.contacts__link {
  color: #444444;
  text-decoration-color: #63d1bb;
  text-underline-offset: 5px;
}
.contacts__link:hover {
  color: #63d1bb;
  transition: 250ms;
}
.contacts__link:active {
  text-decoration-color: rgba(70, 193, 174, 0.1);
}
@media (min-width: 768px) {
  .contacts__link {
    text-underline-offset: 7px;
  }
}
@media (min-width: 1150px) {
  .contacts__link {
    text-underline-offset: 13px;
  }
}

.contacts__adress {
  font-style: normal;
}

.contacts__map {
  grid-area: contacts__map;
  padding-top: 62px;
}
@media (min-width: 768px) {
  .contacts__map {
    padding-top: 47px;
  }
}
@media (min-width: 1150px) {
  .contacts__map {
    padding-top: 98px;
  }
}

.contacts__map iframe {
  border: none;
  display: block;
  height: 457px;
  width: 260px;
}
@media (min-width: 768px) {
  .contacts__map iframe {
    width: 100%;
  }
}
@media (min-width: 1150px) {
  .contacts__map iframe {
    width: 457px;
  }
}

.contacts__write {
  grid-area: contacts__write;
  padding: 42px 0 39px;
}
@media (min-width: 768px) {
  .contacts__write {
    padding: 56px 219px 57px;
  }
}
@media (min-width: 1150px) {
  .contacts__write {
    padding: 0;
    padding-top: 54px;
    width: 66%;
  }
}

.copyright {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 27px;
  padding: 21px 0 20px;
}
@media (min-width: 768px) {
  .copyright {
    padding-top: 15px;
  }
}

.copyright__text {
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
}

.copyright__link {
  display: block;
}

.copyright__link svg {
  fill: #63d1bb;
}
.copyright__link svg:hover {
  fill: #46c1ae;
  transition: 250ms;
}
.copyright__link svg:active {
  opacity: 0.3;
}

.field {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  margin: 0;
  margin-bottom: 22px;
}
.field:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .field {
    flex-direction: row;
    align-items: center;
    margin-bottom: 24px;
  }
  .field--phone, .field--email {
    display: flow-root;
  }
}

.field__label {
  font-weight: 700;
  line-height: 18px;
  color: #000000;
  display: flex;
  position: relative;
}
@media (min-width: 768px) {
  .field__label {
    font-size: 19px;
    margin-right: 26px;
  }
  .field__label--phone, .field__label--email {
    margin-right: 0;
  }
}
@media (min-width: 768px) {
  .field__label {
    margin-right: 24px;
  }
  .field__label--phone, .field__label--email {
    margin-right: 0;
  }
}

.field__label--phone svg {
  right: -5px;
  top: -3px;
  position: absolute;
  transform: translate(-50%, 50%);
  opacity: 0.2;
}
@media (min-width: 768px) {
  .field__label--phone svg {
    right: 14px;
    top: -1px;
  }
}
@media (min-width: 768px) {
  .field__label--phone svg {
    right: -4px;
  }
}

.field__label--email svg {
  right: -13px;
  top: 5px;
  position: absolute;
  transform: translate(-50%, 50%);
  opacity: 0.2;
}
@media (min-width: 768px) {
  .field__label--email svg {
    right: 7px;
    top: 5px;
  }
}
@media (min-width: 768px) {
  .field__label--email svg {
    right: -12px;
    top: 5px;
  }
}

.field__input {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 18px;
  color: #444444;
  width: 100%;
  padding: 16px 23px 15px 0;
  border: none;
  outline: none;
  background-color: transparent;
  border-bottom: 2px solid #000000;
}
.field__input::placeholder {
  color: #000000;
  opacity: 0.3;
}
.field__input:hover, .field__input:focus {
  border-color: #63d1bb;
  transition: 250ms;
}
@media (min-width: 768px) {
  .field__input {
    font-size: 19px;
    padding: 10px 23px 14px 18px;
  }
}

.field__input:hover + svg,
.field__input:focus + svg {
  fill: #62d1ba;
  opacity: 1;
  transition: 250ms;
}

.field__textarea {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 18px;
  width: 100%;
  padding: 17px;
  padding-bottom: 13px;
  outline: 0;
  border: 2px solid #000000;
}
.field__textarea::placeholder {
  opacity: 0.3;
}
.field__textarea:hover, .field__textarea:focus {
  border-color: #63d1bb;
  transition: 250ms;
}
@media (min-width: 768px) {
  .field__textarea {
    font-size: 19px;
    padding: 14px 17px 16px 14px;
  }
}

.form-container__oder {
  margin-bottom: 55px;
}
@media (min-width: 768px) {
  .form-container__oder {
    margin-top: 69px;
    margin-bottom: 50px;
  }
}
@media (min-width: 1150px) {
  .form-container__oder {
    margin-top: 119px;
    margin-bottom: 49px;
  }
}

.hero {
  background-color: #f1f1f1;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  position: relative;
}
.hero--index {
  background-image: url("../img/background/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% -18px;
  background-color: #f9f9f9;
}
@media (min-resolution: 2dppx) {
  .hero--index {
    background-image: url("../img/background/hero-mobile@2x.jpg");
    background-size: 320px auto;
  }
}
@media (min-width: 768px) {
  .hero::after {
    content: "";
    position: absolute;
    height: 24px;
    width: 100%;
    bottom: -14px;
    left: 5px;
    background-image: url("../img/hero-zigzag-fill-tablet.svg");
    background-repeat: repeat-x;
    z-index: 2;
  }
  .hero--index {
    background-image: url("../img/background/hero-desktop@1x.jpg");
    background-size: auto;
  }
  .hero--index::after {
    bottom: -8px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero--index {
    background-image: url("../img/background/hero-desktop@2x.jpg");
    background-size: 768px auto;
  }
}
@media (min-width: 1150px) {
  .hero::after {
    background-image: url("../img/hero-zigzag-fill-desktop.svg");
    height: 34px;
    bottom: -20px;
    left: -25px;
  }
  .hero--index::after {
    bottom: -19px;
    left: -31px;
  }
}

.hero__title {
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  color: #000000;
  margin: 0 auto;
  width: 320px;
  text-align: center;
  padding: 73px 0;
}
.hero__title--index {
  padding: 72px 30px 138px 30px;
}
@media (min-width: 768px) {
  .hero__title {
    width: 768px;
    font-size: 43px;
    line-height: 50px;
    padding: 101px 0 72px;
  }
  .hero__title--index {
    padding: 155px 50px 494px;
  }
}
@media (min-width: 1150px) {
  .hero__title {
    padding: 101px 0 93px;
    font-size: 53px;
    line-height: 57px;
  }
  .hero__title--index {
    width: 1150px;
    padding: 157px 50px 472px 50px;
  }
}

.main-catalog {
  margin-top: -18px;
}
@media (min-width: 768px) {
  .main-catalog {
    margin-top: -123px;
    margin-bottom: 18px;
  }
}
@media (min-width: 1150px) {
  .main-catalog {
    margin-top: -117px;
    margin-bottom: 68px;
  }
}

@media (min-width: 768px) {
  .main-catalog__wrapper {
    width: 768px;
    margin: 0 auto;
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .main-catalog__wrapper {
    width: 1150px;
    padding: 0 50px;
    margin: 0 auto;
  }
}

.main-catalog__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .main-catalog__list {
